.conmtiner_my {
    margin: 0 auto;
    width: 1320px;
    padding: 0 10px;

}
ul{
    list-style: none;
}
.systema {
    background: url(../img/first_fon_img.svg);
    padding: 188px 0 197px ;
    text-align: center;
    h1 {
        font-weight: 900;
        font-size: 31px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 1.4px;
        margin-bottom: 12px;
        color: #fff;
    }
    p {
        font-weight: 700;
        font-size: 21px;
        line-height: 18px;
